import React from 'react';
import { Helmet } from 'react-helmet';
import favicon16 from '../../images/favicon16.png';

const MetaTags = (props) => {
  return (
      <Helmet
      htmlAttributes={{
        lang: 'en'
      }}
      link={[
          { rel: 'icon', type: 'image/png', sizes: "16x16", href: `${favicon16}` },
          { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
          { rel: 'preconnect', href: 'https://fonts.gstatic.com', crossorigin: 'anonymous'},
          { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;900&display=swap', "media":"print", "onload":"this.media='all'"}
      ]}
      script={ process.env.NODE_ENV === "production" ? 
        [
         { "src": "https://cdn.splitbee.io/sb.js", "type": "text/javascript", "defer": 'defer', "data-no-cookie": "true", "data-token": "9X2C0J2T9O5J"} ,
          { "src": "/assets/font-awesome/solid.js", "type": "text/javascript", "defer": 'defer'},
          { "src": "/assets/font-awesome/fontawesome.js", "type": "text/javascript", "defer": 'defer' }
        ] : 
        [
          { "src": "/assets/font-awesome/solid.js", "type": "text/javascript", "defer": 'defer' },
          { "src": "/assets/font-awesome/fontawesome.js", "type": "text/javascript", "defer": 'defer' }
        ]
      }
       />
  )
}

export default MetaTags;