import React from "react";
import styled from "styled-components";

const StyledDownloadLink = styled.a`
    position: relative;
    display: inline;
    text-decoration: inherit;
    color: inherit;
    font-size: var(--font-size-m);
    font-weight: var(--font-weight-semibold);
    background-image: linear-gradient(var(--color-primary-1), var(--color-primary-1));
    background-position: 0% 135%;
    background-repeat: no-repeat;
    background-size: 100% 0.4rem;
    transition: background-size var(--transition-fast);
    transition-delay: 0.1s;
    overflow: visible;
    z-index: 0;
    cursor: pointer;

    &:hover, &:focus, &:active {
        background-size: 100% 0.6rem;
    }

    & svg.svg-inline--fa {
        margin-left: var(--spacing-xs);
        margin-right: var(--spacing-xxs);
    }
`;

const DownloadLink = ({ children, to }) => {
    return (
        <StyledDownloadLink
            href={to}
            data-splitbee-event="Download file"
            data-splitbee-event-filepath={to}
        >
            {children}
            <i className="fa-solid fa-file-arrow-down" />
        </StyledDownloadLink>
    )
}   

export default DownloadLink;