import React from 'react';
// Components
import MetaTags from '../base/page-meta';
import GlobalStyle from '../base/globalstyle';
import { NavBar } from '../base/navbar';
import Footer from '../base/footer';
import { LayoutPageContainer, LayoutWrapper } from './layout';
import SEOMeta from '../base/seo';


const Page = (props) => {
    return (
        <>
            <MetaTags 
                title={props.title}
            />
            <SEOMeta
                title={props.title}
                description={props.description}
                image={props.image}
            />
                <GlobalStyle />
                <LayoutWrapper>
                    <NavBar active={props.active} />
                    <LayoutPageContainer>
                        {props.children}
                    </LayoutPageContainer>
                    <Footer />
                </LayoutWrapper>
        </>
    )
}

export default Page;